.footer {
  background-color: $color-black-russian;
  box-sizing: border-box;
  margin-top: auto;

  @include vp-767 {
    position: relative;
  }
}

.footer__container {
  padding-top: 41px;
  padding-bottom: 35px;

  @include vp-1199 {
    padding-top: 45px;
    padding-bottom: 37px;
    padding-right: 39px;
  }

  @include vp-767 {
    padding-top: 46px;
    padding-bottom: 45px;
  }
}

.footer__logo {
  padding-top: 4px;

  @include vp-767 {
    position: absolute;
    left: 15px;
    top: 45px;
  }
}

.footer__navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include vp-767 {
    flex-direction: column;
  }
}

.footer__navigation-list {
  display: flex;
  flex-wrap: wrap;
  padding-top: 2px;

  @include vp-1199 {
    padding-top: 0;
  }

  @include vp-767 {
    min-width: 305px;
    align-self: flex-start;
  }
}

.footer__social-list {
  text-align: end;
  display: flex;
  flex-wrap: wrap;

  @include vp-767 {
    margin-top: 25px;
    align-self: flex-start;
  }
}

.footer__social-list li {
  &:hover {
    opacity: 0.8;
  }

  &:not(:last-child) {
    margin-right: 11px;
  }
}

.footer__social-list a {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: box-shadow 0.3s;
  outline-color: transparent;

  &:hover,
  &:focus {
    box-shadow: 0 0 10px $color-default-white;
    transition: all 0.3s;
  }
}
