.trainers {
  padding-top: 118px;
  padding-bottom: 120px;
  max-width: 100%;
  margin: 0 auto;

  @include vp-1199 {
    padding-top: 100px;
    padding-bottom: 102px;
  }

  @include vp-767 {
    padding: 69px 0 50px;
  }
}

.trainers h2 {
  margin-bottom: 52px;

  @include vp-1199 {
    text-align: center;
    margin-bottom: 39px;
  }

  @include vp-767 {
    margin-bottom: 43px;
  }
}

.trainers__slider-wrapper {
  display: flex;
  position: relative;
  max-width: 1272px;
  padding: 0 56px;
  min-width: 0;
  margin: 0 auto;

  @include vp-1199 {
    max-width: 950px;
    padding: 0 101px;
  }

  @include vp-767 {
    max-width: 500px;
    padding: 0 47px;
  }
}

.trainers__slider {
  width: 100%;
  min-width: 0;
}

.trainers__slide {
  width: 260px;
  height: 294px;
  min-width: 0;

  @include vp-767 {
    min-height: 274px;
  }
}

.trainers__slider-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 34px;
  background: url("../img/svg/arrow-slider.svg") no-repeat;
  background-position: center;
  border: none;
  background-color: $color-transparent;
  margin: auto 0;
  padding: 0;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }

  @include vp-1199 {
    width: 101px;
  }

  @include vp-767 {
    width: 16px;
    height: 19px;
    background-position: center;
  }

  &--next {
    right: 0;

    @include vp-1199 {
      transform: translateY(-64%);
      right: 3px;
    }

    @include vp-767 {
      margin-right: 12px;
      transform: translateY(-47%);
    }
  }

  &--prev {
    left: 0;
    transform: rotate(180deg) translateY(50%);

    @include vp-1199 {
      transform: rotate(180deg) translateY(64%);
      left: 3px;
    }

    @include vp-767 {
      margin-left: 12px;
      transform: rotate(180deg) translateY(47%);
    }
  }
}
